<template>
  <div class="flex-row-start flex-wrap">
    <div class="iconBox" :class="activeIndex == index ? 'active' : ''" @click="activeIcon(index)" v-for="(item,index) in iconList" :key="index">
      <svg-icon class="svgIcon" :icon-class="item.iconUrl" />
    </div>
    <div style="margin-top:40px;width:100%;text-align:center;">
      <el-button @click="onlyclose">返 回</el-button>
      <el-button type="primary" @click="subMit">确 定</el-button>
    </div>
  </div>
</template>
<script>

import { getShopNavigationIconList } from '@/api/shop/navBar'
export default {
  props: ["iconName"],
  watch:{
    iconName(newvalue,oldValue){
      console.log('newvalue',newvalue)
      console.log('oldValue',oldValue)
      if(newvalue){
        this.activeIndex = this.iconList.findIndex(item => item.iconUrl == newvalue)
      }
    }
  },
  data(){
    return{
      iconList:[],
      activeIndex:''
    }
  },
  created(){
    console.log('iconName',this.iconName)
    this.getList();
  },
  methods:{
    getList(){
      getShopNavigationIconList().then(res=>{
        console.log('获取列表',res)
        res.data.forEach( e=> e.active = false)
        this.iconList = res.data
        console.log('iconName',this.iconName)
        this.activeIndex = this.iconList.findIndex(item => item.iconUrl == this.iconName)
        console.log('activeIndex',this.activeIndex)
      })
    },
    activeIcon(i){
      this.activeIndex = i
    },
    subMit(){
      this.$emit('subMit',this.iconList[this.activeIndex]);
    },
    onlyclose(){
      this.$emit('onlyclose',true);
    },
    
  }
}
</script>
<style lang="less" scoped>
.iconBox{
  width: 50px;
  height: 50px;
  margin: 0 10px 10px 0;
  flex-wrap: wrap;
  background-color: #909399;
  display: flex;
  cursor: pointer;
  .svgIcon{
    width: 25px;
    height: 25px;
    margin: auto;
    color: #fff;
  }
  &.active{
    background-color: #409EFF;
  }
}
</style>