<template>
  <div class="pageContent">
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="8" :offset="2">
          <el-card class="box-card box">
            <div class="nullBox">
              <div class="nullHead">
                <img :src="require('../../../assets/images/iphone-title-info.png')" alt="">
              </div>
              <div class="btnBox">
                <el-button :disabled="navBar.length == 5" type="primary" @click="addBtn">添加导航</el-button>
              </div>
            </div>
            <div class="nav" v-show="navBar.length != 0">
              <el-popover :data-i="activeIndex + ':' + index " :style="{backgroundColor:activeIndex == index ? item.activeBgc : item.bgc}" class="btn" placement="bottom" trigger="hover" v-for="(item,index) in navBar" :key="index" >
                <div @click="activeIcon(index)" slot="reference">
                  <div class="iconBox">
                    <svg-icon :icon-class="item.iconClass" :style="{color:activeIndex == index ? item.activeTextColor : item.textColor}"/>
                  </div>
                  <div class="word" :style="{color:activeIndex == index ? item.activeTextColor : item.textColor}">{{item.name}}</div>
                </div>
                <div style="text-align:center;">
                  <el-button type="text" icon="el-icon-delete" @click="deleteBtn(index)">删除</el-button>
                </div>
              </el-popover>
            </div>
          </el-card>
        </el-col>
        <el-col :span="12">
          <el-form ref="form" :model="form" label-width="140px">
            <el-card class="box-card">
              <el-form-item label="导航名称：" required>
                <el-input style="width:400px;" v-model.trim="ruleForm.name" maxlength="15" show-word-limit></el-input>
              </el-form-item>
              <el-tabs v-show="this.activeIndex != '-1'" v-model="activeName">
                <el-tab-pane label="导航编辑" name="1">
                  <el-form-item label="编辑名称：" required>
                    <el-input style="width:300px;" v-model="form.name" maxlength="4" show-word-limit></el-input>
                  </el-form-item>
                  <el-form-item label="编辑图标：">
                    <div class="iconShow" @click="iconListShowFun(form.iconClass)">
                      <svg-icon class="svgicon" :icon-class="form.iconClass"/>
                    </div>
                  </el-form-item>
                  <el-form-item label="编辑链接：">
                    <el-button @click="navListShowFun">{{form.linkName}}</el-button>
                  </el-form-item>
                </el-tab-pane>
                <el-tab-pane label="背景颜色" name="2">
                  <div style="margin-bottom:10px;">未选中状态</div>
                  <el-form-item label="内容颜色：">
                    <el-color-picker v-model="form.textColor" @active-change="e => form.textColor = e"></el-color-picker>
                  </el-form-item>
                  <el-form-item label="背景颜色：">
                    <el-color-picker v-model="form.bgc" @active-change="e => form.bgc = e"></el-color-picker>
                  </el-form-item>
                  <el-divider></el-divider>
                  <div style="margin-bottom:10px;">选中状态</div>
                  <el-form-item label="内容颜色：">
                    <el-color-picker v-model="form.activeTextColor" @active-change="e => form.activeTextColor = e"></el-color-picker>
                  </el-form-item>
                  <el-form-item label="背景颜色：">
                    <el-color-picker v-model="form.activeBgc" @active-change="e => form.activeBgc = e"></el-color-picker>
                  </el-form-item>
                </el-tab-pane>
              </el-tabs>
            </el-card>
          </el-form>
        </el-col>
      </el-row>
      <div class="flex-row-center" style="margin-top:40px;">
        <el-button type="primary" :loading="submitLoading" @click="subForm">保 存</el-button>
        <el-button @click="backPage">返 回</el-button>
      </div>
    </el-card>
    <!-- icon列表弹出框 -->
    <el-dialog title="选择店铺导航样式" :visible.sync="iconListShow" width="50%">
      <iconList 
      :iconName="selectIcon"
      @onlyclose="onlyclose"
      @subMit="subMit"></iconList>
    </el-dialog>
    <!-- nav列表弹出框 -->
    <el-dialog title="" :visible.sync="navListShow" width="50%">
      <navList 
        @subMitNav="subMitNav"
      ></navList>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import iconList from './components/iconList'
import navList from './components/navList'
import { isShopNavigationExist , insertShopNavigation } from '@/api/shop/navBar'

export default {
  data(){
    return{
      submitLoading:false,
      ruleForm:{},
      activeName:'1',
      navBar:[
        {
          iconClass:'home1',
          name:'一级导航',
          textColor:'#666',
          activeTextColor:'#f12d22',
          bgc:'#fff',
          activeBgc:'#fff',
          linkName:'功能页面 - 首页',
          linkUrl:"/shopIndex",
          id:''
        },
        {
          iconClass:'home1',
          name:'一级导航',
          textColor:'#666',
          activeTextColor:'#f12d22',
          bgc:'#fff',
          activeBgc:'#fff',
          linkName:'功能页面 - 首页',
          linkUrl:"/shopIndex",
          id:''
        },
        {
          iconClass:'home1',
          name:'一级导航',
          textColor:'#666',
          activeTextColor:'#f12d22',
          bgc:'#fff',
          activeBgc:'#fff',
          linkName:'功能页面 - 首页',
          linkUrl:"/shopIndex",
          id:''
        }
      ],
      form:{},
      activeIndex:0,                    // 底部导航选中的索引
      iconListShow:false,               // icon弹出框
      selectIcon:'',                    // 

      navListShow:false,
      selectNav:'',
    }
  },
  created(){
    this.renderForm()
  },
  methods:{
    subForm(){
      if(!this.ruleForm.name){
        this.$message.error('请输入导航名称');
        return;
      }
      console.log('navBar',this.navBar)
      let errArr = this.navBar.filter(item => !item.name)
      if(errArr.length > 0){
        this.$message.error('请输入导航名称');
        return;
      }
      this.ruleForm.content = JSON.stringify(this.navBar);
      this.ruleForm.shopId = this.shop.shopId;
      console.log('请求的数据',this.ruleForm)
      this.submitLoading = true;
      isShopNavigationExist(this.ruleForm).then(res=>{
        console.log('是否重名',res)
        if(res.code == 200){
          insertShopNavigation(this.ruleForm).then(res1=>{
            console.log('新建导航',res1)
            if(res1.code == 200){
              this.$message({
                message: '创建成功',
                type: 'success'
              });
              setTimeout(()=>{
                this.$router.replace({path:'/shop/shopNavBar'})
                this.submitLoading = false;
              },1000)
            }else{
              this.submitLoading = false;
              this.$message.error(res.message);
            }
          })
        }else{
          this.submitLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    backPage(){
      this.$confirm('确定放弃新建导航吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.replace({path:'/shop/shopNavBar'})
      }).catch(() => {});
    },
    navListShowFun(e){
      this.navListShow = true;
      this.selectNav = e.linkName
    },
    iconListShowFun(e){
      console.log('e',e)
      this.selectIcon = e
      this.iconListShow = true;
    },
    // 选择导航
    activeIcon(i){
      this.activeIndex = i
      this.renderForm()
    },
    // 添加导航
    addBtn(){
      console.log('navBar',this.navBar)
      this.navBar.push({
        iconClass:'home1',
        name:'一级导航',
        textColor:'#666',
        activeTextColor:'#f12d22',
        bgc:'#fff',
        activeBgc:'#fff',
        linkName:'功能页面 - 首页',
        linkUrl:"/shopIndex",
        id:''
      })
      if(this.navBar.length == 1) this.activeIndex = 0
      this.renderForm()
    },
    // 删除底部导航
    deleteBtn(i){
      this.navBar.splice(i,1)
      if(i == this.activeIndex) this.activeIndex = 0
      if(this.navBar.length == 0) this.activeIndex = '-1'
      this.renderForm()
    },
    subMit(e){
      console.log('父级确定icon',e)
      this.form.iconClass = e.iconUrl
      this.iconListShow = false;
    },
    subMitNav(e){
      console.log('父级确定nav',e)
      this.form.linkName = e.linkName
      this.form.linkUrl = e.linkUrl
      this.form.parmas = e.parmas
      this.navListShow = false;
    },
    onlyclose(e){
      this.iconListShow = false;
    },
    renderForm(){
      if(this.activeIndex != '-1'){
        let i = this.activeIndex;
        this.form = this.navBar[i]
      }
    },
    change(e){
      this.$forceUpdate()
    }
  },
  components: {
    iconList,
    navList
  },
  computed: {
    ...mapState([
      'shop',
      'user'
    ]),
  },
}
</script>
<style lang="less" scoped>
  .box{
    width: 375px;
    position: relative;
    .icon{
      color: red;
    }
    .nullBox{
      width: 100%;
      height: 667px;
      display: flex;
      .nullHead{
        width: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background-color: black;
        img{
          width: 100%;
        }
      }
      .btnBox{
        margin: auto;
      }
    }
    .nav{
      border-top: 1px solid #909399;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 50px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      .btn{
        cursor: pointer;
        width: 100%;
        height: 100%;
        text-align: center;
        flex:1;
        .iconBox{
          height:21px;
          line-height: 21px;
          margin-top: 6px;
        }
        .word{
          margin-top: 4px;
          height: 14px;
          line-height: 14px;
          font-size: 12px;
        }
      }
      
    }
  }
  .iconShow{
    width:50px;
    height:50px;
    display: flex;
    cursor: pointer;
    background-color: #909399;
    
    .svgicon{
      color: #fff;
      margin:auto;
    }
  }
</style>